.All {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: [top] 20vh [home-start] 80vh [home-end] 20vh [about-start] 80vh [about-end] 20vh [contact-start] 80vh [contact-end];
  grid-template-areas: "nav1" "home" "nav2" "about" "nav3" "contact";

}

.LandingPage {
  background-image: url(./Images/CompressedMain.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  grid-area: top / 1 / home-end / 1;
  z-index: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.LandingTopBlock {
  flex: 5; /*change to 3 when logo is changed to text*/
}

.LandingBotBlock {
  flex: 3;
}

