.Tagline {
  flex: 1;
  color: white;
}

.Tagfont {
  font-size: 5vmin;
  font-family: 'Raleway', sans-serif;
}

.Navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  z-index: 1;
}

.Nav1 {
  grid-area: top / 1 / home-start / 1;
}

.Nav2 {
  grid-area: home-end / 1 / about-start / 1;
}

.Nav3{
  grid-area: about-end / 1 / contact-start / 1;
}

.NavLeftBlock {
  flex: 6;
}

.NavItem {
  flex: 1;
  color: #6f0000;
  font-size: 3vmin;
  font-family: 'Raleway', sans-serif;
  text-align: center;
  transition-duration: 0.2s;
}

.NavItem:hover {
  color: #00263b;
  cursor: pointer;
  font-weight: bolder;
}

.NavItem > .Active {
  border-bottom: 1px solid black;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-area: home-start / 1 / home-end / 1;
  z-index:1;
}

.About {
  display: grid;
  grid-area: about-start / 1 / about-end / 1;
  grid-template-columns: 2.5% 35% repeat(4,1fr) 2.5% ;
  grid-template-rows: 100%;
  grid-template-areas: "lmargin lblock left cleft cright right rmargin"
}

.Contact {
  margin: 10%;
  text-align: center;
}

.AboutLeftBlock {
  background-color: inherit;
  flex: 1;
  grid-area: lblock;
  place-content: space-evenly;
  list-style: none;
  margin: 0;
  padding-left: 0;
}

.SuitPic {
  background-image: url(./Images/Main.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: inherit;
  grid-row: 1;
  grid-column: cleft/ span 2;
  z-index: 3;
}

.London {
  background-image: url(./Images/LondonShadow.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: inherit;
  grid-row: 1;
  grid-column: left/ span 2;
  z-index: 2;
}

.Taj {
  background-image: url(./Images/TajShadow.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: inherit;
  grid-row: 1;
  grid-column: cright/ span 2;
  z-index: 1;
}

.SlideLeft {
  animation: 1.5s ease-out 0s 1 slideToLeft;
}

.SlideRight {
  animation: 1.5s ease-out 0s 1 slideToRight;
}

.Appear{
  animation: 1.5s ease-out 0s 1 appear;
}

@keyframes slideToLeft {
  0% {
      transform: translateX(50%);
  }
  100% {
      transform: translateX(0%);
  }
}

@keyframes slideToRight {
  0% {
      transform: translateX(-50%);
  }
  100% {
      transform: translateX(0%);
  }
}

@keyframes appear {
  from {filter: contrast(0%);}
  to {filter: contrast(100%);}
}

.ScreenWide {
  width: 100vw;
  max-width: 100%;
}

.ScreenHigh {
  height: 100vh;
  max-height: 100%;}

.BackColour {
  background-image: linear-gradient(to bottom, #ffe8df, #ffffff);
}

.Transparent {
  opacity: 0;
}

.AboutLeftBlock > li {
  color: #442727;
  font-size: 115%;
  font-family: 'Raleway', sans-serif;
  margin: 2ch;
}

.Contact > p {
  color: #442727;
  font-size: 200%;
  font-family: 'Raleway', sans-serif;
}

.AboutLeftBlock > li > a {
  background-color: #f0daa4;
  color: inherit;
  text-decoration: none;
}

.AboutLeftBlock > li > a::after {
  background-color: #f0daa4;
  color: inherit;
  text-decoration: none;
}

.AboutLeftBlock > li > a:hover {
  text-decoration: underline;
}

#ResumeButton {
  display: inline-block;
  border-radius: 4px;
  background-color: inherit;
  border: none;
  color: inherit;
  text-align: left;
  font-size: inherit;
  outline: 0;
  width: auto;
  transition: all 0.5s;
  cursor: pointer;
}

#ResumeButton span {
  cursor: pointer;
  outline: 0;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

#ResumeButton span:after {
  content: '\2b73';
  position: relative;
  opacity: 0;
  top: -3ch;
  left: 0.25em;
  transition: 0.5s;
}

#ResumeButton:hover span {
  padding-right: 25px;
}

#ResumeButton:hover span:after {
  opacity: 1;
  top:0;
}
